.link-inset {
  position: absolute;
  inset: 0;
  z-index: 2;
}

.link-hover-line {
  display: inline;
}
.link-hover-line span {
  position: relative;
}
.link-hover-line span::after {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 0;
  right: 0;
  width: 0;
  height: 3px;
  background-color: rgba(245, 245, 245, 0.6);
  transition: width 0.3s ease;
}
.link-hover-line:hover span::after {
  width: 100%;
}

@media (min-width: 1080px) {
  .link-hover-bottom:hover > span::after, .link-hover-bottom:focus > span::after {
    opacity: 1;
    transform: translateY(0px);
    transition: 0.3s ease;
  }
}
.link-hover-bottom > span {
  position: relative;
}
.link-hover-bottom > span::after {
  content: "";
  height: 3px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -3px;
  background-color: rgba(245, 245, 245, 0.6);
  opacity: 0;
  transform: translateY(3px);
}

