/**
 *  Select2 override
 */
.select2-search {
  display: none;
}

.dropdown-pdf {
  border: 1px solid #cacaca;
  border-radius: 15px !important;
  overflow: hidden;
}
.dropdown-pdf .select2-results {
  margin-right: 3px;
}
.dropdown-pdf ul.select2-results__options {
  padding: 8px 0;
}
.dropdown-pdf .select2-results__option {
  font-size: 12px;
  color: #232323;
  padding: 0 12px 0 42px;
  position: relative;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.dropdown-pdf .select2-results__option + .select2-results__option {
  margin-top: 8px;
}
.dropdown-pdf .select2-results__option::before {
  content: "PDF";
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 16px;
  border-radius: 3px;
  background-color: #fa3737;
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  font-family: "Roboto Condensed", sans-serif;
  font-size: 10px;
  color: #ffffff;
  text-transform: uppercase;
}
.dropdown-pdf .select2-results__option--highlighted.select2-results__option--selectable {
  background-color: transparent;
  color: #293bdb;
  transition: 0.3s ease;
}
@media (min-width: 1080px) {
  .dropdown-pdf .select2-results__option--highlighted.select2-results__option--selectable:hover, .dropdown-pdf .select2-results__option--highlighted.select2-results__option--selectable:focus {
    color: #293bdb;
  }
}
.dropdown-pdf .select2-results__option--selected {
  background-color: transparent;
}

/**
 *  Custom scrollbar in dropdown
 */
.select2-container--default .select2-results > .select2-results__options::-webkit-scrollbar {
  width: 2px;
  border-radius: 25px;
}
.select2-container--default .select2-results > .select2-results__options::-webkit-scrollbar-track {
  background-color: transparent;
}
.select2-container--default .select2-results > .select2-results__options::-webkit-scrollbar-thumb {
  background: #cacaca;
}

/**
 *  Custom datepicker ui
 */
.ui-datepicker {
  width: 20.6em;
  padding: 0;
}
.ui-datepicker .ui-datepicker-header {
  padding: 0.5em 0;
  border: none;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  background-color: #f0f0f0;
}
.ui-datepicker .ui-datepicker-next,
.ui-datepicker .ui-datepicker-prev {
  width: 1.5em;
  height: 1.5em;
  top: 9px;
  border-radius: 5px;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='14' height='24' viewBox='0 0 14 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M1 1.00006L12 12.0001L1 23.0001' stroke='%23000000' stroke-width='2'/%3e%3c/svg%3e ");
  background-repeat: no-repeat;
  background-position: center center;
  transition: 0.3s ease;
}
.ui-datepicker .ui-datepicker-next span,
.ui-datepicker .ui-datepicker-prev span {
  display: none;
}
.ui-datepicker select.ui-datepicker-month,
.ui-datepicker select.ui-datepicker-year {
  height: 22px;
  border-color: #cacaca;
  background-color: transparent;
  font-family: "Inter", sans-serif;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.02em;
  color: #000000;
}
.ui-datepicker .ui-datepicker-title {
  line-height: 1.8em;
}
.ui-datepicker .ui-datepicker-prev {
  left: 14px;
  transform: rotate(180deg);
}
.ui-datepicker .ui-datepicker-next {
  right: 14px;
}
.ui-datepicker .select.ui-datepicker-month {
  margin-right: 2px;
}
.ui-datepicker select.ui-datepicker-year {
  margin-left: 2px;
}
.ui-datepicker table {
  width: calc(100% - 1.4em);
  margin: 0 0.7em 0.9em;
}
.ui-datepicker th {
  padding: 1em 0.3em;
  font-size: 13px;
  font-weight: 400;
  color: #0a00ff;
}
.ui-datepicker td {
  border-radius: 3px;
  transition: 0.3s ease;
}
.ui-datepicker td span,
.ui-datepicker td a {
  padding: 0.5em;
  color: #232323;
}
@media (min-width: 1080px) {
  .ui-datepicker td:hover, .ui-datepicker td:focus {
    background-color: #f0f0f0;
  }
}
.ui-datepicker td:hover {
