.search-page__top, .search-page__top-col, .search-page__date {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media only screen and (max-width: 1279px) {
  .search-page__top, .search-page__top-col, .search-page__date {
    justify-content: center;
  }
}
.search-page__top {
  margin-top: 64px;
  gap: 16px;
}
@media only screen and (max-width: 639px) {
  .search-page__top {
    margin-top: 30px;
  }
}
.search-page__top-col {
  gap: 16px;
}
.search-page__top-col:last-child {
  display: flex;
  justify-content: flex-end;
  flex-grow: 1;
}
@media only screen and (max-width: 1279px) {
  .search-page__top-col:last-child {
    justify-content: center;
  }
}
.search-page__date {
  gap: 25px;
  flex-wrap: nowrap;
}
@media only screen and (max-width: 639px) {
  .search-page__date {
    width: 100%;
  }
}
.search-page__field-search {
  min-width: 522px;
  max-width: 522px;
  width: 100%;
}
@media only screen and (max-width: 639px) {
  .search-page__field-search {
    min-width: auto;
    max-width: 100%;
  }
}
.search-page__field-date {
  width: 160px;
}
@media only screen and (max-width: 639px) {
  .search-page__field-date {
    width: calc(50% - 12.5px);
  }
}
.search-page__field-sort, .search-page__link {
  display: flex;
  align-items: center;
  height: 38px;
  position: relative;
  font-size: 14px;
  line-height: 16px;
  color: #232323;
  text-transform: uppercase;
  cursor: pointer;
  transition: 0.3s ease;
}
@media (min-width: 1080px) {
  .search-page__field-sort:hover, .search-page__field-sort:focus, .search-page__link:hover, .search-page__link:focus {
    color: #ffffff;
    background-color: #0a00ff;
  }
  .search-page__field-sort:hover svg, .search-page__field-sort:focus svg, .search-page__link:hover svg, .search-page__link:focus svg {
    stroke: #f5f5f5;
  }
}
.search-page__field-sort svg, .search-page__link svg {
  stroke: #121212;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  transition: 0.3s ease;
}
.search-page__field-sort {
  justify-content: center;
  width: 284px;
  padding: 4px 40px;
}
@media only screen and (max-width: 639px) {
  .search-page__field-sort {
    width: 100%;
  }
}
.search-page__field-sort input {
  display: none;
}
.search-page__field-sort input:checked + span + svg {
  transform: translateY(-50%) rotate(180deg);
}
.search-page__link {
  justify-content: space-between;
  padding: 4px 37px 4px 20px;
}
.search-page__link + .search-page__link {
  margin-top: 15px;
}
.search-page__link svg {
  transform: translateY(-50%) rotate(-90deg);
}
.search-page__wrapper {
  display: flex;
  align-items: flex-start;
  gap: 134px;
  margin-top: 64px;
}
@media only screen and (max-width: 1279px) {
  .search-page__wrapper {
    gap: 40px;
  }
}
@media only screen and (max-width: 989px) {
  .search-page__wrapper {
    flex-direction: column;
    margin-top: 16px;
  }
}
.search-page__col:first-child {
  width: 270px;
}
@media only screen and (max-width: 989px) {
  .search-page__col:first-child {
    width: 100%;
  }
}
.search-page__col:last-child {
  flex-grow: 1;
}
@media only screen and (max-width: 989px) {
  .search-page__col:last-child {
    width: 100%;
  }
}
.search-page__attachment {
  margin-top: 15px;
}
.search-page__select {
  margin-top: 15px;
}
.search-page__select > p {
  font-size: 16px;
  line-height: 21px;
  letter-spacing: 0.01em;
  color: #000000;
  margin-bottom: 7px;
  padding-left: 4px;
}
.search-page__select select {
  width: 100%;
  height: 38px;
}
.search-page__select .select2-container--default .select2-selection--single {
  height: 38px;
}
.search-page__news {
  margin-top: 36px;
}
@media only screen and (max-width: 639px) {
  .search-page__news {
    margin-top: 20px;
  }
}
.search-page__news-item + .search-page__news-item {
  margin-top: 36px;
}
@media only screen and (max-width: 639px) {
  .search-page__news-item + .search-page__news-item {
    margin-top: 20px;
  }
}
.search-page__news-item .card-news__pdf {
  margin-top: 23px;
}
.search-page__pagination {
  margin-top: 65px;
}
@media only screen and (max-width: 639px) {
  .search-page__pagination {
    margin-top: 30px;
  }
