.director-page__wrapper {
  display: flex;
  gap: 87px;
  margin-top: 64px;
}
@media only screen and (max-width: 989px) {
  .director-page__wrapper {
    flex-direction: column;
    gap: 30px;
  }
}
@media only screen and (max-width: 639px) {
  .director-page__wrapper {
    margin-top: 30px;
  }
}
.director-page__text {
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.02em;
  color: #232323;
}
@media only screen and (max-width: 639px) {
  .director-page__text {
    font-size: 14px;
  }
}
.director-page__text p + p {
  margin: 8px 0 0;
}
.director-page ul.list {
  margin: 16px 0 32px;
}
.director-page__thanks {
  font-weight: 600;
  font-style: italic;
  margin-top: 8px;
}
.director-page__thanks p + p {
  margin: 8px 0 0;
}
.director-page__image {
  min-width: 528px;
  max-width: 528px;
  height: 0;
  padding-top: 50%;
  border-radius: 22px;
  overflow: hidden;
  position: relative;
}
@media only screen and (max-width: 989px) {
  .director-page__image {
    min-width: auto;
    width: 100%;
    margin: 0 auto;
    padding-top: 80%;
  }
