@media only screen and (max-width: 639px) {
  .director-page__image {
    padding-top: 100%;
  }
}
.director-page__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  inset: 0;
}
.director-page__image-caption {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #ffffff;
  border-radius: 28px;
  min-width: 303px;
