.director-page__image-caption span:first-child {
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.02em;
  color: #232323;
}
.director-page__image-caption span:last-child {
  font-size: 12px;
  line-height: 14px;
  font-weight: 300;
  color: #828282;
